.header-HomeMobile {
    height: 70px;
    background: #fff;
    box-shadow: 0px 0 2px rgb(177, 177, 177);
}

.header-HomeMobile h3 {
    position: relative;
    font-size: 16px;
    left: 10px;
}

.header-HomeMobile h1 {
    position: relative;
    font-size: 20px;
    color: #000;
    font-weight: bold;
    left: 10px;
}

.header-HomeMobile .profilImg {
    background: rgb(161, 197, 194);
    position: absolute;
    top: 10px;
    right: 10px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    float: right;
}

.card-list {
    margin-top: 2em;
    overflow-x: auto;
    display: flex;
    flex-direction: row;
}

.customer-card {
    background: #192d87cc;
    color: #fff;
    border: 1px solid rgb(155, 155, 155);
    border-radius: 15px;
    margin: 5px;
    min-width: 230px;
    padding: 5px;
    height: 120px;
}

.customer-card h4 {
    margin-top: 5px;
    margin-left: 10px;
}

.customer-card h4 span {
    float: right;
    font-size: 12px;
}

.customer-card p {
    margin-top: 50px;
    margin-left: 10px;
}

.recap {
    background: #fff;
    border-radius: 10px;
    margin: 20px auto;
    display: flex;
    width: 80vw;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}

.recap-arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 15px;
}

.recap-arrow-up {
    background: #DCEDF1;
}

.recap-arrow-down {
    background: #DDDFEB;
}

.recap-card {
    align-items: center;
    width: 150px;
    display: flex;
    justify-content: space-evenly;
}

.recap-card span {
    flex-wrap: wrap;
}